
.flexbox {
  /* Safari */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.flexbox.center {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.flexbox.vertical {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.float.left {
  float: left;
}
.float.right {
  float: right;
}
.clear-float:after {
  clear: both;
  content: '.';
  display: block;
  width: 0;
  height: 0;
  visibility: hidden;
}
.margin-5 {
  margin: 5px;
}
.margin-10 {
  margin: 10px;
}
.margin-20 {
  margin: 20px;
}
.margin-30 {
  margin: 30px;
}
.padding-5 {
  padding: 5px;
}
.padding-10 {
  padding: 10px;
}
.padding-20 {
  padding: 20px;
}
.padding-30 {
  padding: 30px;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: #666666;
  background: #f9f9fb;
}
body::-webkit-scrollbar {
    display: none;
}
body *:focus {
  outline: none !important;
}
h1.center,
h2.center,
h3.center,
h4.center {
  text-align: center;
}
ul {
  list-style-type: none;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-padding-start: 0;
}
#app {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","SimSun",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}



